/* -------------------------

Title: campfire
Descr: 
    sets up main campfire, three columns, and responsive breakdown

--------------------------- */
.campfire {
  @include baseGrid();
  grid-row-gap: 0;
  grid-template-columns: 1fr;
  margin-bottom: var(--component-bottom-margin);
  > div {
    display: flex;
    flex-direction: column;
  }
  // Medium Plus
  @include medium() {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas:
      "one one two"
      "three three three";
    &__articles:nth-child(2n + 2) {
      grid-area: two;
    }
    // far right column drops down in middle range
    &__articles:nth-child(3n + 3) {
      grid-area: three;
    }
    &__feature {
      grid-area: one;
    }
  }
  // Large Plus
  @include large() {
    // grid-template-columns: repeat(3, 1fr) repeat(6, 0.75fr) repeat(3, 1fr);
    grid-template-columns: repeat(12, 1fr);
    grid-template-areas: "one one one two two two two two two three three three";
    &__articles:nth-child(2n + 2) {
      grid-area: one;
    }
    // reset display on third column
    &__articles:nth-child(3n + 3) {
      grid-area: three;
    }
    &__feature {
      grid-area: two;
    }
  }
  // @include xl {
  //   grid-template-columns: repeat(12, 1fr);
  // }
}
// campfire specific styles
.campfire__support {
  @include boxShadow;
  border-radius: var(--border-radius);
  background: var(--gray-lightest);
  padding: var(--space);
  margin-bottom: 2rem;
  p {
    @include xl {
      font-size: 1.25rem;
    }
  }
  h2 {
    font-weight: var(--heavy);
    margin-top: 0.5rem;
  }
}
